#root {
  text-align: center;
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.joystick-container {  
  max-width: 600px;
  max-height: 600px;
  width: 100vw;
  height: 100vh;
  position: relative;
  cursor: pointer;
  -ms-touch-action: none;
      touch-action: none;
  -webkit-tap-highlight-color: transparent;  
}
.canvas {
  background-color: rgb(240, 240, 240);
  border: 1px solid rgb(40, 40, 40);
  border-radius: 3px;
  position: absolute;
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  z-index: -1;
  overflow: hidden;
}
.canvas > div {
  position: relative;
  border: 1px solid rgb(40, 40, 40);
  display: none;
}
.canvas > div > div {
  position: relative;
  background-color: rgb(40, 40, 40);
}


